.header {
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: right;
    justify-content: space-between;
    background-color: #77002e;
    padding: 0 10%;
  }

  .header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
  }

  .header li {
    margin-left: 3rem;
  }

  .header a {
    text-decoration: none;
    font-size: 25px;
    color: white;
  }

  .header a:hover {
      color: black;
  }
  